import React, { useState } from 'react';
import styled from 'styled-components';
import * as XLSX from 'xlsx';
import jsPDF from 'jspdf';

const FormContainer = styled.div`
  max-width: 600px;
  margin: 2rem auto;
  padding: 2rem;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
`;

const Input = styled.input`
  width: 100%;
  padding: 0.5rem;
  margin: 0.5rem 0;
  border: 1px solid #ddd;
  border-radius: 4px;
`;

const Select = styled.select`
  width: 100%;
  padding: 0.5rem;
  margin: 0.5rem 0;
  border: 1px solid #ddd;
  border-radius: 4px;
`;

const Button = styled.button`
  width: 100%;
  padding: 0.75rem;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin: 0.5rem 0;
  
  &:hover {
    background-color: #0056b3;
  }
`;

const ErrorMessage = styled.div`
  color: red;
  margin: 0.5rem 0;
`;

function CertificateForm() {
  const [userName, setUserName] = useState('');
  const [participationCode, setParticipationCode] = useState('');
  const [course, setCourse] = useState('');
  const [events, setEvents] = useState([]);
  const [selectedEvent, setSelectedEvent] = useState('');
  const [error, setError] = useState('');
  const [excelFile, setExcelFile] = useState(null);

  const handleFileUpload = (e) => {
    const file = e.target.files[0];
    const reader = new FileReader();
    
    reader.onload = (event) => {
      try {
        const workbook = XLSX.read(event.target.result, { type: 'binary' });
        setEvents(workbook.SheetNames);
        setExcelFile(workbook);
        setError('');
      } catch (err) {
        setError('Invalid Excel file format');
      }
    };
    
    reader.readAsBinaryString(file);
  };

  const validateParticipationCode = () => {
    if (!excelFile || !selectedEvent) return false;
    
    const worksheet = excelFile.Sheets[selectedEvent];
    const data = XLSX.utils.sheet_to_json(worksheet);
    
    return data.some(row => row.participationCode === participationCode);
  };

  const generateCertificate = () => {
    const doc = new jsPDF({
      orientation: 'landscape',
      unit: 'mm',
      format: 'a4'
    });

    // Add certificate background
    doc.setFillColor(240, 240, 240);
    doc.rect(0, 0, 297, 210, 'F');

    // Add certificate content
    doc.setFontSize(30);
    doc.setTextColor(0, 0, 0);
    doc.text('Certificate of Participation', 148.5, 40, { align: 'center' });

    doc.setFontSize(16);
    doc.text('This is to certify that', 148.5, 70, { align: 'center' });

    doc.setFontSize(24);
    doc.setFont('helvetica', 'bold');
    doc.text(userName, 148.5, 90, { align: 'center' });

    doc.setFontSize(16);
    doc.setFont('helvetica', 'normal');
    doc.text('has successfully participated in', 148.5, 110, { align: 'center' });

    doc.setFontSize(20);
    doc.setFont('helvetica', 'bold');
    doc.text(selectedEvent, 148.5, 130, { align: 'center' });

    doc.setFontSize(16);
    doc.setFont('helvetica', 'normal');
    doc.text(`Course: ${course}`, 148.5, 150, { align: 'center' });

    // Save the PDF
    doc.save(`${userName}_certificate.pdf`);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setError('');

    if (!excelFile) {
      setError('Please upload an Excel file first');
      return;
    }

    if (!validateParticipationCode()) {
      setError('Invalid participation code');
      return;
    }

    generateCertificate();
  };

  return (
    <FormContainer>
      <form onSubmit={handleSubmit}>
        <Input
          type="file"
          accept=".xlsx,.xls"
          onChange={handleFileUpload}
          required
        />

        <Input
          type="text"
          placeholder="Enter your name"
          value={userName}
          onChange={(e) => setUserName(e.target.value)}
          required
        />
        
        <Input
          type="text"
          placeholder="Participation Code"
          value={participationCode}
          onChange={(e) => setParticipationCode(e.target.value)}
          required
        />
        
        <Input
          type="text"
          placeholder="Course Name"
          value={course}
          onChange={(e) => setCourse(e.target.value)}
          required
        />
        
        <Select
          value={selectedEvent}
          onChange={(e) => setSelectedEvent(e.target.value)}
          required
        >
          <option value="">Select Event</option>
          {events.map((event, index) => (
            <option key={index} value={event}>
              {event}
            </option>
          ))}
        </Select>
        
        {error && <ErrorMessage>{error}</ErrorMessage>}
        
        <Button type="submit">Generate Certificate</Button>
      </form>
    </FormContainer>
  );
}

export default CertificateForm;